import React, { useContext, useEffect, useState } from "react";
import {
  Box,
  Container,
  Grid,
  InputBase,
  styled,
  Typography,
  useMediaQuery,
} from "@mui/material";
import { useWeb3Modal } from "@web3modal/wagmi/react";
import moment from "moment";
import { useSwitchNetwork } from "wagmi";
import { fetchBalance } from "@wagmi/core";
import { AppContext } from "../utils";
import { ethLogo, logo } from "../components/SmallComponents/Images";
import {
  StyledButton,
  ToastNotify,
} from "../components/SmallComponents/AppComponents";
import TimerCountDown from "../components/SmallComponents/PresaleTimer";
import {
  preSaleReadFunction,
  preSaleWriteFunction,
} from "../ConnectivityAssets/hooks";
import { formatUnits, parseUnits } from "viem";
import Loading from "../components/SmallComponents/loading";

const Paragraph = styled(Typography)({
  fontWeight: 400,
  fontSize: "17px",
  fontFamily: "Noto Sans",
  lineHeight: "25px",
  color: "#000000",
});

const Paragraph2 = styled(Typography)({
  fontFamily: "'Noto Sans'",
  fontStyle: "normal",
  fontWeight: 400,
  fontSize: "12px",
  lineHeight: "19px",
  color: "#000000",
});

const MianHeading = styled(Typography)({
  fontFamily: "'Noto Sans'",
  fontStyle: "normal",
  fontWeight: 700,
  fontSize: "36px",
  lineHeight: "128.2%",
  color: "#000000",
});

const TextInput = styled(InputBase)({
  "& .MuiInputBase-input": {
    height: "40px",
    fontSize: "15px",
    fontFamily: "Noto Sans",
    fontWeight: "700",
    textAlign: "left",
    color: "#000000",
    backgroundColor: "transparent",
    paddingLeft: "15px",
    "&::-webkit-outer-spin-button": {
      WebkitAppearance: "none",
      margin: 0,
    },
    "&::-webkit-inner-spin-button": {
      WebkitAppearance: "none",
      margin: 0,
    },
  },
});

const PresalePage = () => {
  const matches = useMediaQuery("(max-width:700px)");
  const { open } = useWeb3Modal();
  const { switchNetwork } = useSwitchNetwork();
  const { account, chainId } = useContext(AppContext);
  const [enteredAmount, setEnteredAmount] = useState("");
  const [receivedTokens, setreceivedTokens] = useState("");
  const [hardcap, sethardcap] = useState(0);
  const [balanceETH, setbalanceETH] = useState(0);
  const [raisedAmount, setRaisedAmount] = useState(0);
  const [progressBar, setProgessBar] = useState(0);
  const [loading, setloading] = useState(false);
  const [maxBuyETH, setmaxBuyETH] = useState(0);
  const [isClaimEnabled, setisClaimEnabled] = useState(false);
  const [userPurchasedTokens, setuserPurchasedTokens] = useState(0);
  const [userPurchasedTokensShow, setuserPurchasedTokensShow] = useState("");
  const [callFunction, setCallFunction] = useState(true);
  const [contractTimer, setContractTimer] = useState(0);
  const [contractTimerStatus, setContractTimerStatus] = useState("");
  let currentTime = moment().format("X");

  const [alertState, setAlertState] = useState({
    open: false,
    message: "",
    severity: undefined,
  });
  const toLocalFormat = (val) => {
    return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  const init = async () => {
    try {
      const hardcap = await preSaleReadFunction("hardCap");
      const raised = await preSaleReadFunction("amountRaised");
      sethardcap(+formatUnits(hardcap.toString(), 18));
      let prog =
        (+formatUnits(raised.toString(), 18) /
          +formatUnits(hardcap.toString(), 18)) *
        100;
      setProgessBar(prog);
      let raise = parseFloat(+formatUnits(raised.toString(), 18)).toFixed(2);
      const claimEnabled = await preSaleReadFunction("canClaim");
      setisClaimEnabled(claimEnabled);
      setRaisedAmount(toLocalFormat(raise));
      setCallFunction(false);
    } catch (error) {
      setCallFunction(false);
      console.log(error, "ERROR VoidSigner Data");
    }
  };
  useEffect(() => {
    (async () => {
      let startTimeContract = await preSaleReadFunction("preSaleStartTime");
      startTimeContract = +startTimeContract?.toString();
      let endTimeContract = await preSaleReadFunction("preSaleEndTime");
      endTimeContract = +endTimeContract?.toString();
      if (currentTime <= startTimeContract) {
        setContractTimer(startTimeContract);
        setContractTimerStatus("Starts");
      } else {
        setContractTimer(endTimeContract);
        setContractTimerStatus("Ends");
      }
      try {
      } catch (error) {
        console.log(error, "time");
      }
    })();
  }, []);
  useEffect(() => {
    init();
  }, [callFunction, chainId]);

  const initAccount = async () => {
    try {
      const userInvested = await preSaleReadFunction("userContribution", [
        account,
      ]);
      const balETH = await fetchBalance({
        address: account,
      });
      setbalanceETH(+formatUnits(balETH?.value?.toString(), 18));
      const userPurchased = await preSaleReadFunction("userTokenBalance", [
        account,
      ]);
      let tokens = parseFloat(
        +formatUnits(userPurchased.toString(), 18)
      ).toFixed(0);
      setuserPurchasedTokens(+formatUnits(userPurchased.toString(), 18));
      setuserPurchasedTokensShow(toLocalFormat(tokens));
    } catch (error) {}
  };
  useEffect(() => {
    if (account) {
      initAccount();
    }
  }, [account]);
  useEffect(() => {
    const calculator = async () => {
      try {
        let tokensPerEth = +750000000 * +enteredAmount;
        setreceivedTokens(tokensPerEth);
      } catch (error) {}
    };
    if (+enteredAmount > 0) {
      calculator();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [enteredAmount]);
  const buyHadler = async () => {
    if (account) {
      if (!enteredAmount) {
        setAlertState({
          open: true,
          message: `Error! Please enter an amount`,
          severity: "error",
        });
      } else if (enteredAmount <= 0) {
        setAlertState({
          open: true,
          message: `Error! Please enter a valid amount`,
          severity: "error",
        });
      } else {
        try {
          setloading(true);
          await preSaleWriteFunction(
            "buyToken",
            [],
            parseUnits(enteredAmount.toString(), 18).toString()
          );
          setAlertState({
            open: true,
            message: "Success! Transaction Confirmed",
            severity: "success",
          });
          setEnteredAmount("");
          setreceivedTokens(0);
          setloading(false);
          initAccount();
        } catch (error) {
          setloading(false);
          setAlertState({
            open: true,
            message: error?.shortMessage,
            severity: "error",
          });
        }
      }
    } else {
      setAlertState({
        open: true,
        message: `Error! Please connect your wallet.`,
        severity: "error",
      });
    }
  };
  const claimHandler = async () => {
    if (account) {
      if (!isClaimEnabled) {
        setAlertState({
          open: true,
          message: `You can claim tokens after presale will end.`,
          severity: "error",
        });
      } else if (+chainId !== 1) {
        setAlertState({
          open: true,
          message: `You are on wrong network switch to Ethereum`,
          severity: "error",
        });
      } else if (+userPurchasedTokens <= 0) {
        setAlertState({
          open: true,
          message: `You don't have tokens to claim.`,
          severity: "error",
        });
      } else {
        try {
          setloading(true);
          await preSaleWriteFunction("claim");
          setAlertState({
            open: true,
            message: `Transaction Completed!`,
            severity: "success",
          });
          setloading(false);
          setCallFunction(true);
          initAccount();
        } catch (error) {
          setloading(false);
          setAlertState({
            open: true,
            message: error?.shortMessage,
            severity: "error",
          });
        }
      }
    } else {
      setAlertState({
        open: true,
        message: `Error! Please connect your wallet.`,
        severity: "error",
      });
    }
  };
  return (
    <>
      <Loading loading={loading} />
      <ToastNotify alertState={alertState} setAlertState={setAlertState} />
      <Box py={5}>
        <Container maxWidth="lg">
          <Grid
            container
            spacing={3}
            justifyContent="center"
            alignItems="center"
            display="flex"
          >
            <Grid item md={7} xs={12}>
              <Box>
                <MianHeading mt={3}>Trollface Presale</MianHeading>
                <Paragraph my={4} pr={matches ? 0 : 2}>
                  1 ETH = 750,000,000 $TROLL <br /> <br />
                  Fair launch <br />
                  <br />
                  100 ETH HC - 3 eth Max buy
                </Paragraph>
                <Box display="flex" justifyContent="center">
                  <img
                    // style={{ transform: "scaleX(-1)" }}
                    src={logo}
                    alt=""
                    width="300px"
                  />
                </Box>
              </Box>
            </Grid>

            {/* Presale stage 1 Card data  */}

            <Grid item md={5} xs={12}>
              <Box
                sx={{
                  boxShadow:
                    "inset 0 2px 9px rgba(255, 255, 255, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4)",
                  position: "relative",
                  backdropFilter: "blur(9px)",
                  borderRadius: "10px",
                  textAlign: "center",
                }}
              >
                <Box
                  sx={{
                    p: 3,
                  }}
                >
                  <Typography
                    sx={{
                      fontFamily: "'Noto Sans'",
                      fontStyle: "normal",
                      fontWeight: 700,
                      fontSize: "25px",
                      lineHeight: "42px",
                      color: "#0000000",
                      textTransform: "uppercase",
                    }}
                  >
                    Presale {contractTimerStatus} In
                  </Typography>
                  <TimerCountDown time={contractTimer} />
                  <Box
                    sx={{
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                      pb: 1,
                    }}
                    mt={3}
                  >
                    <Paragraph2
                      sx={{
                        fontSize: matches ? "20px" : "20px",
                        fontWeight: "700",
                      }}
                    >
                      1 ETH = 750,000,000 $TROLL
                    </Paragraph2>
                  </Box>
                  <div className="meter">
                    <span
                      style={{
                        width: `${+progressBar > 0 ? +progressBar : 0}%`,
                      }}
                    ></span>
                    <Box
                      sx={{
                        position: "absolute",
                        top: "4%",
                        left: "43%",
                        color: "#ffffff",
                        fontSize: "20px",
                        fontWeight: "600",
                      }}
                    >
                      {+progressBar > 0
                        ? parseFloat(progressBar).toFixed(3)
                        : "0.00"}
                      %
                    </Box>
                  </div>
                  <Box mb={2}>
                    <Box
                      sx={{
                        display: "flex",
                        justifyContent: "center",
                        mt: 2,
                      }}
                    >
                      <Paragraph2
                        sx={{
                          fontSize: matches ? "15px" : "20px",
                          display: "flex",
                          justifyContent: "center",
                          alignItems: "start",
                        }}
                      >
                        ETH RAISED - {parseFloat(raisedAmount).toFixed(3)} ETH
                      </Paragraph2>
                    </Box>
                  </Box>
                  <Box
                    display="flex"
                    alignItems="center"
                    justifyContent="space-between"
                    width="100%"
                  >
                    <Box
                      textAlign="center"
                      mb={0.5}
                      fontWeight="500"
                      color="gray"
                      mt={2}
                      width="47%"
                      borderRadius="10px"
                      py={1}
                      boxShadow="inset 0 2px 9px rgba(255, 255, 255, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4)"
                    >
                      ETH Balance <br /> {parseFloat(balanceETH).toFixed(4)}
                    </Box>
                    <Box
                      textAlign="center"
                      mb={0.5}
                      fontWeight="500"
                      color="gray"
                      mt={2}
                      width="47%"
                      borderRadius="10px"
                      py={1}
                      boxShadow="inset 0 2px 9px rgba(255, 255, 255, 0.3), inset 0 -2px 6px rgba(0, 0, 0, 0.4)"
                    >
                      $TROLL Balance <br />{" "}
                      {userPurchasedTokens > 0
                        ? userPurchasedTokensShow
                        : "0.00"}
                    </Box>
                  </Box>
                  <Box
                    sx={{
                      boxShadow: "2px 3px 10px 3px #d1d1d1",
                      borderRadius: "8px",
                      width: "100%",
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center",
                      mt: 2,
                    }}
                  >
                    <TextInput
                      fullWidth
                      placeholder="Enter amount"
                      type="number"
                      value={enteredAmount}
                      onChange={(e) => setEnteredAmount(e.target.value)}
                    />
                    <Box
                      fontFamily="Noto Sans"
                      fontWeight={700}
                      fontSize="18px"
                      pr={1}
                      display="flex"
                      alignItems="center"
                    >
                      <img
                        src={ethLogo}
                        alt=""
                        style={{
                          marginRight: "4px",
                          width: "25px",
                        }}
                      />
                    </Box>
                  </Box>
                  <Box
                    sx={{
                      boxShadow: "2px 3px 10px 3px #d1d1d1",
                      borderRadius: "8px",
                      width: "100%",
                      display: "flex",
                      justifyContent: "space-between",
                      alignItems: "center",
                      my: 2,
                    }}
                  >
                    <TextInput
                      fullWidth
                      placeholder="Enter amount"
                      type="number"
                      value={
                        +enteredAmount > 0
                          ? parseFloat(receivedTokens).toFixed(2)
                          : ""
                      }
                    />
                    <Box
                      fontFamily="Noto Sans"
                      fontWeight={700}
                      fontSize="18px"
                      pr={1}
                      display="flex"
                      alignItems="center"
                    >
                      <img
                        src={logo}
                        alt=""
                        style={{
                          marginRight: "4px",
                          width: "25px",
                          height: "30px",
                        }}
                      />
                    </Box>
                  </Box>
                  {+chainId !== 1 && account ? (
                    <StyledButton
                      width="100%"
                      onClick={() => switchNetwork?.(1)}
                    >
                      Switch Network
                    </StyledButton>
                  ) : account && !isClaimEnabled ? (
                    <StyledButton width="100%" onClick={() => buyHadler()}>
                      Buy Now
                    </StyledButton>
                  ) : account && isClaimEnabled ? (
                    <StyledButton width="100%" onClick={() => claimHandler()}>
                      Claim $TROLL
                    </StyledButton>
                  ) : (
                    <StyledButton width="100%" onClick={() => open()}>
                      Connect Wallet
                    </StyledButton>
                  )}
                </Box>
                <Box pb={8} />
                <Box
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                  position="absolute"
                  width="100%"
                  py={1}
                  bottom={0}
                  bgcolor="rgba(0, 0, 0, 0.4)"
                  sx={{
                    borderBottomLeftRadius: "10px",
                    borderBottomRightRadius: "10px",
                  }}
                >
                  <img
                    src={ethLogo}
                    alt=""
                    style={{
                      marginRight: "4px",
                      width: "40px",
                    }}
                  />
                  <img
                    src={logo}
                    alt=""
                    style={{
                      marginLeft: "12px",
                      width: "40px",
                      width: "45px",
                    }}
                  />
                </Box>
              </Box>
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default PresalePage;
